onsidera como aquel escenario en el cual los individuos pueden interactuar entre sí, estando en la posibilidad del intercambio de ideas y pensamiento y al mismo tiempo evidencia las conductas de cada uno de los miembros que conforman determinado contexto. “En este emergen múltiples formas de relacionarse, discusión e intercambio de ideas, conceptos e imaginarios interpersonales” (Zapata, 2018).")])]),_c('div',{staticClass:"col-md-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img8.png"),"alt":"Imagen deocrativa"}})])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"El contexto social "}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Es el conjunto de circunstancias que enmarcan una situación que afecta a uno o más individuos. El mismo hecho puede ocurrir en un entorno u otro y su análisis y evaluación cambiarán dependiendo de lo que sea (Torres, et al., 2003). Se puede crecer en una familia específica, vivir en una ciudad determinada, tener relaciones con ciertos amigos y moverse en un círculo de relaciones que es lo que se conoce como el contexto social con el que un individuo interactúa de manera inmediata. ")])]),_c('div',{staticClass:"col-md-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img9.png"),"alt":"Imagen deocrativa"}})])])])])],1)]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Este texto aborda cómo las interacciones con los demás y con el entorno contribuyen al desarrollo de competencias ciudadanas, sociales y laborales, facilitando la adaptación a los desafíos diarios, promoviendo el equilibrio entre las convicciones personales y las necesidades colectivas.")]),_c('separador'),_vm._m(8),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('PasosA',{staticClass:"color-acento-contenido mb-5",attrs:{"tipo":"l"}},[_c('div',{staticClass:"row",attrs:{"titul